.movies {
  padding: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.movie {
  width: 30%;
  max-width: 320px;
  position: relative;
  margin-bottom: 30px;
  border-radius: 4px;
  overflow: hidden;
  cursor: crosshair;
}
.movie > li {
  backface-visibility: hidden;
  transition: 0.5s;
}

.movie li.movie-Card__front img {
  display: block;
  width: 100%;
}

.movie li.movie-Card__back {
  padding: 24px 20px;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  transform: rotateY(180deg);
}
.movie:hover li.movie-Card__front {
  transform: rotateY(180deg);
}
.movie:hover li.movie-Card__back {
  height: 100%;
  background-color: white;
  transform: rotateY(0deg);
}

.movie li.movie-Card__back .movie__title {
  font-size: 24px;
  margin-bottom: 4px;
}
.movie li.movie-Card__back .movie__title span {
  font-size: 18px;
}
.movie li.movie-Card__back .movie__genres {
  padding-bottom: 6px;
  margin-bottom: 10px;
  border-bottom: 1px solid #dbdbdb;
}
.movie li.movie-Card__back .movie__genres li {
  display: inline-block;
  margin-right: 4px;
  color: #333;
}
.movie li.movie-Card__back .movie__summary {
  font-size: 18px;
  line-height: 1.4em;
  letter-spacing: 0.02em;
  font-weight: 400;
  color: #111;
}

@media screen and (max-width: 900px) {
  .movie {
    width: 100%;
  }
  .movie li.movie-Card__back .movie__title {
    font-size: 5vw;
    margin-bottom: 0px;
  }
  .movie li.movie-Card__back .movie__title span {
    font-size: 3vw;
  }
  .movie li.movie-Card__back .movie__genres li {
    font-size: 3vw;
  }
  .movie li.movie-Card__back .movie__summary {
    font-size: 3.9vw;
  }
}
